<template><section><h1>ArcGISTiledElevationTerrainProvider</h1>
<p>The <code v-pre>vc-provider-terrain-arcgis-tiled-elevation</code> component is used to load terrain geometry by tessellating height maps retrieved from Elevation Tiles of an ArcGIS ImageService.</p>
<h2>Example</h2>
<h3>Load terrain data in ArcGISTiledElevation</h3>
<h4>Preview</h4>
<doc-preview>
  <template>
    <div class="viewer">
      <vc-viewer @ready="ready">
        <vc-layer-imagery>
          <vc-provider-imagery-bingmaps url="https://dev.virtualearth.net" bmKey="AgcbDCAOb9zMfquaT4Z-MdHX4AsHUNvs7xgdHefEA5myMHxZk87NTNgdLbG90IE-" mapStyle="Aerial"></vc-provider-imagery-bingmaps>
        </vc-layer-imagery>
        <vc-provider-terrain-arcgis-tiled-elevation :url="terrainUrl"></vc-provider-terrain-arcgis-tiled-elevation>
      </vc-viewer>
    </div>
  </template>
  
</doc-preview>
