<h2>Instance Properties</h2>
<!-- prettier-ignore -->
<table>
<thead>
<tr>
<th>name</th>
<th>type</th>
<th>default</th>
<th>description</th>
</tr>
</thead>
<tbody>
<tr>
<td>url</td>
<td>String|Object</td>
<td></td>
<td><code v-pre>required</code> The URL of the ArcGIS ImageServer service.</td>
</tr>
<tr>
<td>token</td>
<td>String</td>
<td></td>
<td><code v-pre>optional</code> The authorization token to use to connect to the service.</td>
</tr>
<tr>
<td>ellipsoid</td>
<td>Object</td>
<td></td>
<td><code v-pre>optional</code> The ellipsoid. If the tilingScheme is specified, this parameter is ignored and the tiling scheme's ellipsoid is used instead. If neither parameter is specified, the WGS84 ellipsoid is used.</td>
</tr>
</tbody>
</table>
<hr>
<p>Refer to the official document: <a href="https://cesium.com/docs/cesiumjs-ref-doc/ArcGISTiledElevationTerrainProvider.html">ArcGISTiledElevationTerrainProvider</a></p>
<h2>Events</h2>
<table>
<thead>
<tr>
<th>name</th>
<th>parameter</th>
<th>description</th>
</tr>
</thead>
<tbody>
<tr>
<td>ready</td>
<td>{Cesium, viewer, cesiumObject}</td>
<td>Triggers when the component is ready. It returns a core class of Cesium, a viewer instance, and the cesiumObject.</td>
</tr>
<tr>
<td>errorEvent</td>
<td>TileProviderError</td>
<td>Triggers when the terrain provider encounters an asynchronous error.</td>
</tr>
</tbody>
</table>
<hr>
</section></template>

<script>
    export default {
      data () {
        return {
          terrainUrl: 'https://elevation3d.arcgis.com/arcgis/rest/services/WorldElevation3D/Terrain3D/ImageServer'
        }
      },
      methods: {
        ready (cesiumInstance) {
          const {Cesium, viewer} = cesiumInstance
          var target = new Cesium.Cartesian3(300770.50872389384, 5634912.131394585, 2978152.2865545116)
          var offset = new Cesium.Cartesian3(6344.974098678562, -793.3419798081741, 2499.9508860763162)
          viewer.camera.lookAt(target, offset)
          viewer.camera.lookAtTransform(Cesium.Matrix4.IDENTITY)
        }
      }
    }
  </script>